body {
	margin: 0;
	background-color: #ede3d9;
	background-image: url(.././../img/bg-body.jpg);
	background-repeat: no-repeat;
	background-size: cover;
}

img {
	max-width: 100%;
}

.container {
	overflow: hidden;
}

.block {
	display: block;
}

.price {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin: 0;
	line-height: 1.6;
	font-family: "Roboto Slab", serif;
	text-transform: uppercase;

	& del {
		margin: 0 5px;
		font-size: 14px;
		color: rgb(120, 43, 7);
		text-decoration-line: line-through;
	}

	& span {
		margin: 0 5px;
		font-weight: bold;
		font-size: 20px;
		color: rgb(116, 37, 1);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(1);
		-ms-transform: scale(1);
		transform: scale(1);
	}

	50% {
		-webkit-transform: scale(1.1);
		-ms-transform: scale(1.1);
		transform: scale(1.1);
	}

	100% {
		-webkit-transform: scale(1);
		-ms-transform: scale(1);
		transform: scale(1);
	}
}

@keyframes rotation {
	70% {
		transform: rotate(1860deg);
	}

	to {
		transform: rotate(1827deg);
	}
}

@keyframes rotation-button {
	70% {
		transform: rotate(-1860deg) translate(-50%,-50%);
	}

	to {
		transform: rotate(-1827deg) translate(-50%,-50%);
	}
}



.rotation {
	transition-timing-function: ease-in-out;
	animation-name: rotation;
	animation-duration: 7s;
	animation-fill-mode: forwards;
}

.rotation .form__spin-button {
	transform-origin: center;
	transition-timing-function: ease-in-out;
	animation-name: rotation-button;
	animation-duration: 7s;
	animation-fill-mode: forwards;
}
